@border-color: #e6e6e6;

@font: #666;
@font-dark: #333;
@font-light: #999;
@font-lighter: #ccc;
@font-lightest: #e6e6e6;
//border
@border-solid: 1px @border solid;
@border-radius: 4px;

//size
@font-size: 14px;
@h1: 20px;
