//清浮动
.clearfix() {
  &:before,
  &:after {
    content: '';
    display: table;
  }

  &:after {
    clear: both;
    overflow: hidden;
  }
}

.wrapper {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background-color: @primary;
  text-align: center;
  line-height: 1.2;
  color: #fff;
  font-size: @h4;
  display: flex;
  justify-content: center;
  align-items: center;
  &.mini {
    width: 30px;
    height: 30px;
    -webkit-transform-origin-x: 0;
    transform: scale(0.8);
    font-size: 12px;
  }
}
